<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row">
      <div class="col-lg-5">

        <b-card class="p-3 text-left" no-body  style="max-height:552;min-height:552px">
            <b-row>
              <b-col>
                  <img
                    class="rounded-circle"
                    width="60%"
                    :src="form.imagen"
                    data-holder-rendered="true"
                  />
              </b-col>
            </b-row>
            <blockquote class="blockquote mb-0">
              <h2></h2>
              <h5></h5>
              <p style="margin:0">Codigo: {{form.codigo}} </p>
              <p style="margin:0">Tipología: {{form.tipologia}}</p>
              <p style="margin:0">Entidad: {{form.entidad_bancaria}} </p>
              <p style="margin:0">Nombre:   {{form.terminal}}</p>
              <p style="margin:0">Dirección: {{form.direccion}}</p>
              <p style="margin:0">Ciudad: {{form.ciudad}} </p>
              <p style="margin:0">Regional: {{form.regional}} </p>
              <p style="margin:0">Numero Site: {{form.numero_site}}</p>
              <p style="margin:0">Comparte Site: {{form.comparte_site}}</p>
            </blockquote>
          </b-card>
 
      </div>
      <div class="col-lg-7">
        <Trazabilidad />
      </div>
      <div class="col-lg-12">
        <Album />
      </div>
    </div>
  </Layout>
</template>

<script>

import vue2Dropzone from "vue2-dropzone";
import {mapState,mapMutations, mapActions} from 'vuex'
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import Trazabilidad from './trazabilidad';
import Album from './album';

/**
 * Dashboard component
 */
export default {
  components: {
    vueDropzone: vue2Dropzone,
    Layout,
    PageHeader,
    ValidationProvider,
    ValidationObserver,
    Trazabilidad,
    Album
  },
  data() {
   
     return {
      title: "Procesos",
      items: [
        {
          text: "Cajeros ATH"
        },
        {
          text: "Detalles",
          active: true
        }
      ],
     dropzoneOptions: {
        thumbnailWidth: 150,
        maxFilesize: 0.5,
        headers: { "My-Awesome-Header": "header value" }
      },
      ver:false,
      url:"",
      url_firma:"",
      modal: true,
      file:null,
      firma:null,
      email: "",
      password: "",
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortBy: "age",
      sortDesc: false,
      fields: ["cargo","descripcion","actions"],
      cajero:[{
        'entidad':[{
            'imagen':'',
        }]
      }], 
      cajeros:[],
      editMode:false,
      form:{
          'id':'',
          'tipo':'',
          'codigo':'',
          'tipologia':'',
          'entidad_bancaria':'',
          'terminal':'',
          'direccion':'',
          'tipo_site':'',
          'numero_site':'',
          'mantenimiento':'',
          'comparte_site':'',
          'cumpleanos':'',
          'administrado':'',
          'aseo':'',
          'cierre_nocturno':'',
          'hora_cierre':'',
          'apertura':'',
          'hora_apertura':'',
          'estado':'',
          'imagen':'',
          'regional':'',

      }
    }
  },
  computed:{
        ...mapState(['counter'])
   },
  created(){
    this.listarUsers();
  },
  methods: {

    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }, 
    ...mapActions(['guardarUsuario']),
    switchLoc(){
      if (!this.editMode) {
        this.$refs.form.validate().then(esValido => {
            if (esValido) {
              this.agregarCargo();
            } else {}
          });        
        }else{
          this.$refs.form.validate().then(esValido => {
          if (esValido) {
            this.editarCargos();
          } else {
        }});
      }
    },
   async agregarCargo(){
     let data = new FormData();
      var formulario = this.form;
        for (var key in formulario) {
          data.append(key,formulario[key]);
        }
       await this.axios.post('api/cargos', data, {
           headers: {
            'Content-Type': 'multipart/form-data'
           }}).then(response => {
            if (response.status==200) {
               this.$swal(
                   'Agregado exito!',
                    '',
                    'success');
               this.listarCargos();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
              console.log(e.response.data.menssage);
              this.$swal(e.response.data);
          });
      },
    async editarCargos(){
     let data = new FormData();
       var formulario = this.form;
        for (var key in formulario) {
          data.append(key,formulario[key]);
        }
        await this.axios.put('api/cargos', data).then(response => {
            if (response.status==200) {
               this.$swal('Editado con exito','','success');
               this.listarCargos();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
                this.$swal('ocurrio un problema','','warning');
            });
     },
     async eliminarCargos(id){
        let data = new FormData();
        data.append('id',id);
        await this.axios.post('api/cargos/delete',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
                this.$swal(
                    'Eliminado con exito!',
                      '',
                      'success'
                );
                this.listarCargos();
                }
              }).catch(e => {
                console.log(e.response.data.menssage);
                this.$swal(e.response.data);
          });
      }, 
      eliminarCargo(id){
        this.$swal({
          title: 'Desea borrar este cargo?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.eliminarCargos(id);
          }
        })
      },
      resete(){
        var formulario = this.form;
        for (var key in formulario) {
             this.form[key]="";
       }
      },
      setear(id) {
        for (let index = 0; index < this.cargos.length; index++) {
          if (this.cargos[index].id===id) {
            this.form.id=this.cargos[index].id;
            this.form.cargo=this.cargos[index].cargo;
            this.form.descripcion=this.cargos[index].descripcion;
            this.$root.$emit("bv::show::modal", "modal", "#btnShow");
            return;
          }
        }
      },
    async  listarCajero(){
      let data = new FormData();
       data.append('id',this.$route.params.id);
       await this.axios.post('api/cajeros/ath/byid',data)
        .then((response) => {
          this.cajeros= response.data;
          for (let index = 0; index < this.cajeros.length; index++) {
            console.log(this.cajeros[index].id)
            if (this.cajeros[index].id==this.$route.params.id) {
              this.form.id=this.cajeros[index].id;
              this.form.tipo=this.cajeros[index].tipo;
              this.form.codigo=this.cajeros[index].codigo;
              this.form.tipologia=this.cajeros[index].tipologia;
              this.form.entidad_bancaria=this.cajeros[index].entidad_bancaria;
              this.form.terminal=this.cajeros[index].terminal;
              this.form.direccion=this.cajeros[index].direccion;
              this.form.tipo_site=this.cajeros[index].tipo_site;
              this.form.numero_site=this.cajeros[index].numero_site;
              this.form.mantenimiento=this.cajeros[index].mantenimiento;
              this.form.comparte_site=this.cajeros[index].comparte_site;
              this.form.cumpleanos=this.cajeros[index].cumpleanos;
              this.form.administrado=this.cajeros[index].administrado;
              this.form.aseo=this.cajeros[index].aseo;
              this.form.cierre_nocturno=this.cajeros[index].cierre_nocturno;
              this.form.hora_cierre=this.cajeros[index].hora_cierre;
              this.form.apertura=this.cajeros[index].apertura;
              this.form.hora_apertura=this.cajeros[index].hora_apertura;
              this.form.estado=this.cajeros[index].estado;
              this.form.imagen=this.cajeros[index].entidad.imagen;
              this.form.ciudad=this.cajeros[index].ciudad.ciudad;
              this.form.regional=this.cajeros[index].regional.nombre;
              console.log("hey");
            }
        
            
          }
        })
        .catch((e)=>{
          console.log('error' + e);
        })
      },
      setEmail(){
        this.form.username=this.form.email;
        console.log("holas");
      },
      onFileChange(e) {
        const file = e.target.files[0];
        this.url = URL.createObjectURL(file);
      },
      toggleModal () {
        this.modal = !this.modal
      },
      session(){
        if (localStorage.getItem('token')) {
          const token=localStorage.getItem('token');
          this.guardarUsuario(token);
          }else{
          this.$router.push({ name: 'Home' });
          }
        }
  },
    created(){
      
        this.session();
        

      },
     mounted() {
       this.listarCajero();
    },
    computed: {
    rows() {
      return this.cajeros.length;
    },
  },
}
</script>
